import * as React from 'react'
import { styled, alpha } from '@mui/material/styles'
import Grid from '@mui/material/Grid'
import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import Divider from '@mui/material/Divider'
import ListItemText from '@mui/material/ListItemText'
import ListItemAvatar from '@mui/material/ListItemAvatar'
import Avatar from '@mui/material/Avatar'
import AppBar from '@mui/material/AppBar'
import Box from '@mui/material/Box'
import Toolbar from '@mui/material/Toolbar'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import InputBase from '@mui/material/InputBase'
import MenuIcon from '@mui/icons-material/Menu'
import SearchIcon from '@mui/icons-material/Search'
import SendAndArchiveIcon from '@mui/icons-material/SendAndArchive'
import Sentiment from 'sentiment'
import tweetService from '../services/tweet.service'
import { useNavigate } from 'react-router-dom'
import ReactJson from 'react-json-view'

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: '50%',
  },
}))

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}))

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
  },
}))

const Home = () => {
  const [query, setQuery] = React.useState('')
  const [tweets, setTweets] = React.useState([])
  const [selectedTweet, setSelectedTweet] = React.useState({})
  const navigation = useNavigate()
  const sentiment = new Sentiment()
  const handleChange = (e) => {
    console.log(e.target.value)
    setQuery(e.target.value)
  }

  const handleSubmit = () => {
    console.log(query)
    if (query.length > 2) {
      tweetService
        .getData(query)
        .then((result) => {
          console.log(result.data)
          if (Array.isArray(result.data)) {
            setTweets(result.data)
          }
        })
        .catch((e) => {
          console.log(e)
        })
    }
  }

  const handleClick = (tweet) => {
    console.log(tweet.text.length)
    setSelectedTweet(tweet)
  }

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="open drawer"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              onChange={handleChange}
              placeholder="Search…"
              value={query}
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
          <IconButton
            size="large"
            aria-label="display more actions"
            edge="end"
            color="inherit"
            onClick={handleSubmit}
          >
            <SendAndArchiveIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Grid container spacing={2} style={{ height: '100%', paddingTop: "64px" }}>
        <Grid item xs={6}>
          <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
            {tweets.map((tweet, index) => {
              return (
                <div key={index}>
                  <ListItem
                    alignItems="flex-start"
                    onClick={() => handleClick(tweet)}
                  >
                    <ListItemAvatar>
                      <Avatar
                        alt="Remy Sharp"
                        src="/static/images/avatar/1.jpg"
                      />
                    </ListItemAvatar>
                    <ListItemText
                      primary={tweet.text}
                      secondary={
                        <React.Fragment>
                          <Typography
                            sx={{ display: 'inline' }}
                            component="span"
                            variant="body2"
                            color="text.primary"
                            style={{ overflowWrap: 'anywhere' }}
                          >
                            {JSON.stringify(sentiment.analyze(tweet.text))}
                          </Typography>
                        </React.Fragment>
                      }
                    />
                  </ListItem>
                  <Divider variant="inset" component="li" />
                </div>
              )
            })}
          </List>
        </Grid>
        <Grid item xs={6} style={{textAlign:"initial", position:"fixed", top: "64px", right: 0, width: "100%", height: "calc(100% - 70px)", paddingTop: 0, overflow: "scroll", backgroundColor: "rgb(39, 40, 34)"}}>
          <ReactJson src={selectedTweet} theme="summerfruit" name="Tweet" style={{padding: "16px"}}/>
          <ReactJson src={sentiment.analyze(selectedTweet.text)} theme="monokai" name="AFINN" style={{marginTop: "16px", padding: "16px"}}/>
        </Grid>
      </Grid>
    </Box>
  )
}

export default Home
